.body{
    font-family: poppins;
    margin: 0;
    display: grid;
    font-size: 14px;
}

footer{
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-flow: row wrap;
    padding-left: 5%;
    padding-right: 5%;
    padding-top: 3%;
    padding-bottom: 2%;
    color: #fff;
    background-color:#6faf46;
}
.footer > *{
    flex: 1 1 100%;
}
.footer-left{
    margin-left: 1.25em;
    margin-bottom: 2em;
}
.footer-left img{
    width: 10%;
}
.h2{
    font-weight: 600;
    font-size: 17px;
}
.footer ul{
    list-style: none;
    padding-left: 0;
    display: contents;
}
.footer li{
    line-height: 2em;
}
.footer a{
    text-decoration: none;
}
.footer-right{
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    flex-flow: row-wrap;
}
.footer-right > *{
    flex:1 1 50%;
    margin-right: 1.25em;
}
.box a{
    color:#ffffff;
}

.footer-left p{
    padding-right: 20%;
    color: #ffffff;
}
.socials a{
    /*background: #d7f3c2;*/
    width: 40px;
    height: 40px;
    display: inline-block;
    margin-right: 10px;
}
.socials a i{
    color: #e7f2f4;
    padding: 10px 12px;
    font-size: 20px;
}

@media screen and (min-width:600px){
    .footer-right > *{
        flex: 1 1;
    }
    .footer-left{
        flex:1 1;
    }
    .footer-right{
        flex: 2 1;
    }
}

